export function AppSidebar() {
  const pathname = usePathname()
  const { theme, setTheme } = useTheme()
  const router = useRouter()
  const [mounted, setMounted] = useState(false)
  const [userName, setUserName] = useState("User")

  useEffect(() => {
    setMounted(true)
    const storedUser = localStorage.getItem("ufriends_user")
    if (storedUser) {
      try {
        const user = JSON.parse(storedUser)
        setUserName(user.fullName || user.name || "User")
      } catch (error) {
        console.error("Error parsing user data:", error)
      }
    }
  }, [])

  const handleLogout = () => {
    localStorage.removeItem("ufriends_user")
    router.push("/login")
  }

  return (
    <Sidebar className="bg-neutral-950 text-neutral-200 border-r border-neutral-800" collapsible="icon">
      {/* Header */}
      <SidebarHeader className="border-b border-neutral-800 bg-neutral-900">
        <div className="flex items-center gap-3 px-2">
          <Image src="/ufriend-logo.png" alt="UFriends IT" width={40} height={40} className="rounded-lg" />
          <span className="font-bold text-lg text-neutral-100 group-data-[collapsible=icon]:hidden">
            UFriends IT
          </span>
        </div>
      </SidebarHeader>

      {/* Content */}
      <SidebarContent>
        <SidebarGroup>
          <SidebarGroupContent>
            <SidebarMenu>
              {menuItems.map((item) => {
                const Icon = item.icon
                const hasSubItems = item.subItems?.length > 0
                const isActive = item.href && pathname === item.href
                const hasActiveChild = hasSubItems && item.subItems.some((sub) => pathname.startsWith(sub.href))

                if (item.href && !hasSubItems) {
                  return (
                    <SidebarMenuItem key={item.label}>
                      <SidebarMenuButton
                        asChild
                        isActive={isActive}
                        className={isActive
                          ? "bg-primary/10 text-primary font-medium"
                          : "text-neutral-300 hover:bg-neutral-800 hover:text-neutral-100"}
                      >
                        <Link href={item.href}>
                          <Icon className="h-5 w-5" />
                          <span>{item.label}</span>
                        </Link>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                  )
                }

                if (hasSubItems) {
                  return (
                    <Collapsible key={item.label} defaultOpen={hasActiveChild} className="group/collapsible">
                      <SidebarMenuItem>
                        <CollapsibleTrigger asChild>
                          <SidebarMenuButton
                            className={hasActiveChild
                              ? "bg-primary/10 text-primary font-medium"
                              : "text-neutral-300 hover:bg-neutral-800 hover:text-neutral-100"}
                          >
                            <Icon className="h-5 w-5" />
                            <span>{item.label}</span>
                            <ChevronDown className="ml-auto h-4 w-4 transition-transform group-data-[state=open]/collapsible:rotate-180" />
                          </SidebarMenuButton>
                        </CollapsibleTrigger>
                        <CollapsibleContent>
                          <SidebarMenuSub>
                            {item.subItems.map((subItem) => {
                              const isSubActive = pathname.startsWith(subItem.href)
                              return (
                                <SidebarMenuSubItem key={subItem.href}>
                                  <SidebarMenuSubButton
                                    asChild
                                    isActive={isSubActive}
                                    className={isSubActive
                                      ? "bg-primary/10 text-primary font-medium"
                                      : "text-neutral-400 hover:bg-neutral-800 hover:text-neutral-100"}
                                  >
                                    <Link href={subItem.href}>{subItem.label}</Link>
                                  </SidebarMenuSubButton>
                                </SidebarMenuSubItem>
                              )
                            })}
                          </SidebarMenuSub>
                        </CollapsibleContent>
                      </SidebarMenuItem>
                    </Collapsible>
                  )
                }
              })}
            </SidebarMenu>
          </SidebarGroupContent>
        </SidebarGroup>
      </SidebarContent>

      {/* Footer */}
      <SidebarFooter className="border-t border-neutral-800 bg-neutral-900 space-y-3">
        {mounted && (
          <div className="flex items-center justify-between px-3 py-2 rounded-lg bg-neutral-800">
            <span className="text-sm group-data-[collapsible=icon]:hidden">
              {theme === "light" ? "Light" : "Dark"}
            </span>
            <Switch checked={theme === "dark"} onCheckedChange={() => setTheme(theme === "light" ? "dark" : "light")} />
          </div>
        )}

        <SidebarMenu>
          <SidebarMenuItem>
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <SidebarMenuButton className="bg-neutral-800 hover:bg-neutral-700 text-neutral-200 transition-colors">
                  <Avatar className="h-8 w-8">
                    <AvatarImage src="/user-avatar.png" />
                    <AvatarFallback className="bg-neutral-700 text-neutral-200">UF</AvatarFallback>
                  </Avatar>
                  <div className="flex flex-col group-data-[collapsible=icon]:hidden">
                    <span className="text-sm font-semibold">{userName}</span>
                    <span className="text-xs text-neutral-400">View Profile</span>
                  </div>
                  <ChevronDown className="ml-auto h-4 w-4" />
                </SidebarMenuButton>
              </DropdownMenuTrigger>
              <DropdownMenuContent side="top" className="w-56">
                <DropdownMenuItem asChild>
                  <Link href="/dashboard/settings">Settings</Link>
                </DropdownMenuItem>
                <DropdownMenuItem onClick={handleLogout} className="text-red-600">
                  <LogOut className="mr-2 h-4 w-4" />
                  Logout
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </SidebarMenuItem>
        </SidebarMenu>
      </SidebarFooter>
    </Sidebar>
  )
}
